import STATE_modals from '@/store/STATE_modals.js';
import Functions from '@/logic/Functions.js';

/**
 * Types
*/
class Types
{
	had_error = false;

	/**
	 * is_config_invalid
	*/
	is_config_invalid(config)
	{
		this.validate_session_uuid(config.session_token);
		this.validate_endpoint(config.endpoint_type);
		this.validate_backend(config.backend_type);
		this.validate_mode(config.run_mode);
		this.validate_group(config.group_id);
		this.validate_step(config.step);
		this.validate_flow(config.flow_id);
		this.validate_agent(config.agent_code);
		this.validate_skin(config.skin);
		this.validate_filtered(config.filtered_products);
		this.validate_signature(config.signature_client);
		this.validate_manager(config.cookie_session_manager);
		this.validate_seo(config.refresh_seo_cache);
		this.validate_analytics(config.analytics);
		this.validate_proxy(config.proxy_endpoint);
		this.validate_scope(config.proxy_scope);
		this.validate_exposed(config.save_exposed_state);
		this.validate_fresh(config.get_fresh_session_token_promise);
		// this.validate_access(config.get_access_token_promise); missing
		return this.had_error;
	}

	/**
	 * validate_session_uuid
	*/
	validate_session_uuid(session_token)
	{
		// empty string or 8chars-4chars-4chars-4chars-12chars (char: number or lower case letter)
		let passed = /^$|^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/.test(session_token);
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'session_token', value: session_token, allow: 'dc02bb74-6812-42b7-b418-82882677a732',});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'session_token');
		this.had_error = true;
	}

	/**
	 * validate_endpoint
	*/
	validate_endpoint(endpoint_type)
	{
		let allow = ['minor_stage', 'stage', 'edu', 'production', 'pg_production', 'develop', 'dev',];
		let passed = allow.indexOf(endpoint_type) > -1;
		if (passed) {
			return;
		}
		allow = allow.join(' | ');
		STATE_modals().store_modal_data({error: 'endpoint_type', value: endpoint_type, allow: allow,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'endpoint_type');
		this.had_error = true;
	}

	/**
	 * validate_backend
	*/
	validate_backend(backend_type)
	{
		let allow = ['vue','php_wrapper','np_wp','lukas_local','myport','fp_symfony',];
		let passed = allow.indexOf(backend_type) > -1;
		if (passed) {
			return;
		}
		allow = allow.join(' | ');
		STATE_modals().store_modal_data({error: 'backend_type', value: backend_type, allow: allow,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'backend_type');
		this.had_error = true;
	}

	/**
	 * validate_mode
	*/
	validate_mode(run_mode)
	{
		let allow = ['form_app','node_red','docs_landing',];
		let passed = allow.indexOf(run_mode) > -1;
		if (passed) {
			return;
		}
		allow = allow.join(' | ');
		STATE_modals().store_modal_data({error: 'run_mode', value: run_mode, allow: allow,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'run_mode');
		this.had_error = true;
	}

	/**
	 * validate_group
	*/
	validate_group(group_id)
	{
		let passed = typeof group_id === 'number';
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'group_id', value: group_id, allow: 'int',});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'group_id');
		this.had_error = true;
	}

	/**
	 * validate_step
	*/
	validate_step(step)
	{
		let passed = typeof step === 'number';
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'step', value: step, allow: null,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'step');
		this.had_error = true;
	}

	/**
	 * validate_flow
	*/
	validate_flow(flow_id)
	{
		let passed = typeof flow_id === 'number' || flow_id === undefined;
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'flow_id', value: flow_id, allow: null,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'flow_id');
		this.had_error = true;
	}

	/**
	 * validate_agent
	*/
	validate_agent(agent_code)
	{
		let passed = typeof agent_code === 'string' && (Functions.contains_only_digits(agent_code) || agent_code === '');
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'agent_code', value: agent_code, allow: null,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'agent_code');
		this.had_error = true;
	}

	/**
	 * validate_skin
	*/
	validate_skin(skin)
	{
		let allow = ['finportal','najpoistenie','partners','zeteo','myport','neutral',];
		let passed = allow.indexOf(skin) > -1;
		if (passed) {
			return;
		}
		allow = allow.join(' | ');
		STATE_modals().store_modal_data({error: 'skin', value: skin, allow: allow,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'skin');
		this.had_error = true;
	}

	/**
	 * validate_filtered
	*/
	validate_filtered(filtered_products)
	{
		let passed = filtered_products === undefined || filtered_products === '' || (typeof filtered_products === 'string' && Functions.contains_only_digits(filtered_products.replace(',', '')));
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'filtered_products', value: filtered_products, allow: null,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'filtered_products');
		this.had_error = true;
	}

	/**
	 * validate_signature
	*/
	validate_signature(signature_client)
	{
		// empty string or 8chars-4chars-4chars-4chars-12chars (char: number or lower case letter)
		let passed = signature_client === undefined || /^$|^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/.test(signature_client);
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'signature_client', value: signature_client, allow: '436cee74-2367-4c23-bf1f-2899716e9650',});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'signature_client');
		this.had_error = true;
	}

	/**
	 * validate_manager
	*/
	validate_manager(cookie_session_manager)
	{
		let passed = typeof cookie_session_manager === 'boolean' || cookie_session_manager === undefined;
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'cookie_session_manager', value: cookie_session_manager, allow: null,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'cookie_session_manager');
		this.had_error = true;
	}

	/**
	 * validate_seo
	*/
	validate_seo(refresh_seo_cache)
	{
		let passed = typeof refresh_seo_cache === 'boolean' || refresh_seo_cache === undefined;
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'refresh_seo_cache', value: refresh_seo_cache, allow: null,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'refresh_seo_cache');
		this.had_error = true;
	}

	/**
	 * validate_analytics
	*/
	validate_analytics(analytics)
	{
		let passed = typeof analytics === 'boolean' || analytics === undefined;
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'analytics', value: analytics, allow: null,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'analytics');
		this.had_error = true;
	}

	/**
	 * validate_proxy
	*/
	validate_proxy(proxy_endpoint)
	{
		let passed = (typeof proxy_endpoint === 'string' && proxy_endpoint !== '') || proxy_endpoint === undefined || proxy_endpoint === false;
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'proxy_endpoint', value: proxy_endpoint, allow: null,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'proxy_endpoint');
		this.had_error = true;
	}

	/**
	 * validate_scope
	*/
	validate_scope(proxy_scope)
	{
		let allow = ['b2b','backoffice','b2c','api',];
		let passed = allow.indexOf(proxy_scope) > -1;
		if (passed) {
			return;
		}
		allow = allow.join(' | ');
		STATE_modals().store_modal_data({error: 'proxy_scope', value: proxy_scope, allow: allow,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'proxy_scope');
		this.had_error = true;
	}

	/**
	 * validate_exposed
	*/
	validate_exposed(save_exposed_state)
	{
		let passed = save_exposed_state === undefined || save_exposed_state === null || typeof save_exposed_state === 'function';
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'save_exposed_state', value: save_exposed_state, allow: null,});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'save_exposed_state');
		this.had_error = true;
	}

	/**
	 * validate_fresh
	*/
	validate_fresh(get_fresh_session_token_promise)
	{
		// console.log(get_fresh_session_token_promise);
		// console.log(typeof get_fresh_session_token_promise);
		// console.log(get_fresh_session_token_promise() instanceof Promise);
		let passed = get_fresh_session_token_promise === undefined ||
			get_fresh_session_token_promise === null ||
			(
				typeof get_fresh_session_token_promise === 'function'
				// check this one only when debugging, otherwise it would be called everytime app is initialized
				// && get_fresh_session_token_promise() instanceof Promise
			);
		if (passed) {
			return;
		}
		STATE_modals().store_modal_data({error: 'get_fresh_session_token_promise', value: get_fresh_session_token_promise, allow: 'get_fresh_session_token_promise',});
		STATE_modals().open_it('Invalid_uuid');
		console.log('config error', 'get_fresh_session_token_promise');
		this.had_error = true;
	}
}

export default new Types;
